.goal-card {
  background-color: #fff;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.06);
  padding: 16px;
  margin: 12px 0;
  transition: box-shadow 0.25s ease, transform 0.25s ease;
  will-change: transform;
}

.goal-card:hover {
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.12);
  transform: translateY(-4px) scale(1.02);
  cursor: pointer;
}

.goal-card-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

}

.goal-card-title {
  font-size: 1.25rem;
  font-weight: 600;
  margin: 0;
  color: #333;
}

.goal-progress {
  width: 50px;
  height: 30px;
}

.goal-card-body {
  padding: 1px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin-top: 1rem;
  align-items: flex-end;
}

.goal-card-logo-container {
  display: flex;
  justify-content: center;
}

.goal-card-logo {
  width: 70px;
  height: 70px;
  object-fit: contain;
  margin: 2px auto;
}

.amount-container {
  display: flex;
  gap: 15px;
  align-items: flex-start;
}

.amount-section{
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 1;
}

.divider {
  height: 60px;
  width: 1px;
  background-color: #ccc;
}

.amount-label {
  font-size: 0.9rem;
  font-weight: 500;
  color: #888;
  margin-bottom: 4px;
  text-align: center;
}

.amount-value {
  font-size: 1.5rem;
  font-weight: bold;
  color: #333;
}

.goal-card-actions {
  display: flex;
  gap: 15px;
  justify-content: flex-end;
  margin-top: 0.8rem;
}

.goal-card-actions button {
  padding: 6px 10px;
  border: none;
  border-radius: 6px;
  font-size: 13px;
  cursor: pointer;
  transition: background 0.2s ease;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.1);
}

.goal-edit-btn {
  background-color: #2196f3;
  color: white;
}

.goal-edit-btn:hover {
  background-color: #212121;
}

.goal-delete-btn {
  background-color: #f44336;
  color: white;
}

.goal-delete-btn:hover {
  background-color: #d32f2f;
}